<template>
<b-container>
  <div id="signUp">
    <div v-if="userExists">
      Welcome <span id="pseudo"> {{ pseudo }}</span>. Destroy your account by clicking <a href="#" @click="destroyAccount">here</a>.
    </div>
    <div v-else>
      <span>Sign up <router-link to="/signup">here</router-link>.</span>
    </div>
  </div>
  <h1>{{ msg }}</h1>
  <p>ist ein gesellschaftliches Experiment: Lasst uns spielend gemeinsam eine neue Welt erschaffen! </p>
  <hr>

  <b-row>
    <aboutBox></aboutBox>
    <toolsBox></toolsBox>
  </b-row>
  <hr>
  <b-row>
    <blockchainBox></blockchainBox>
    <crowdfundingBox></crowdfundingBox>
  </b-row>
  <hr>
  <b-button href="./#/level1">zu level 1</b-button>
  <br><br><br>
</b-container>
</template>

<script>
import Users from '@/js/users'
import AboutBox from './AboutBox'
import ToolsBox from './ToolsBox'
import CrowdfundingBox from './CrowdfundingBox'
import BlockchainBox from './BlockchainBox'

export default {
  name: 'welcome',
  components: {
    AboutBox,
    ToolsBox,
    CrowdfundingBox,
    BlockchainBox
  },
  data () {
    return {
      msg: 'transformativ game',
      pseudo: undefined
    }
  },
  computed: {
    userExists: function () {
      return (typeof this.pseudo !== 'undefined')
    }
  },
  beforeCreate: function () {
    if (window.web3.eth.accounts[0] !== undefined) {
      Users.init().then(() => {
        Users.exists(window.web3.eth.accounts[0]).then((exists) => {
          if (exists) {
            Users.authenticate().then(pseudo => {
              this.pseudo = pseudo
            })
          }
        })
      }).catch(err => {
        console.log(err)
      })
    }
  },
  methods: {
    destroyAccount: function (e) {
      e.preventDefault()
      Users.destroy().then(() => {
        this.pseudo = undefined
      }).catch(err => {
        console.log(err)
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

#signUp
{
  position: absolute;
  top: 20px;
  display: none;
}

#pseudo
{
  color: #aaa;
}

</style>

<style>
  .box
  {
  padding: 5px;
  }
</style>



