import Vue from 'vue'
import Router from 'vue-router'
import Welcome from '@/components/Welcome'
import Level1 from '@/components/Level1'
import Level2 from '@/components/Level2'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'welcome',
      component: Welcome
    },
    {
      path: '/level1',
      name: 'level1',
      component: Level1
    },

    {
      path: '/level2',
      name: 'level2',
      component: Level2
    }
  ]
})



