<template>
<b-container>
  <topBar></topBar>
  <div>
    <b-row>
      <b-col>
        <h2>level 2</h2>
        <p>sehr gut! Du bist jetzt mit der Blockchain verbunden. Besorge dir nun deine ersten Tokens!
          Je nachdem, wie du das Projekt unterstützen magst, hast du drei Möglichkeiten:</p>
      </b-col>
    </b-row>
    <br>
    <b-tabs>
      <b-tab title="crowdfunding" active>
        <br>
        <b-row>
          <b-col>
            <h3>variante 1: crowdfunding</h3>
            <p>um das Spiel auszurollen und die daraus folgenden Projekte zu realisieren, brauchen wir - neben vielen motivierten Mitspielern - auch Kapital. Unterstütze das Projekt finanziell indem du am Crowdfunding teilnimmst!</p>
            <p>Der Preis für einen Token liegt bei 0,001 ETH (1 ETH = 1000 Token)</p>
          </b-col>
        </b-row>
        <hr>
        <br>
        <b-row>
          <b-col md="6">
            <div class="box">
              <ol>
                <li v-bind:class="{ solved: addressExist }">erzeuge eine Ethereum-Adresse
                  <b-img v-if="addressExist" src="static/open-iconic/svg/check.svg" alt="check"></b-img>
                </li>
                <li v-bind:class="{ solved: etherBalance > 0 }">besorge dir Ether
                  <b-img v-if="etherBalance > 0" src="static/open-iconic/svg/check.svg" alt="check"></b-img>
                </li>
                <li v-bind:class="{ solved: tokenBalance > 0 }">sende Ether an den Crowdsale Contract
                  <b-img v-if="tokenBalance > 0" src="static/open-iconic/svg/check.svg" alt="check"></b-img>
                </li>
              </ol>
              <p>Falls du das Crowdfunding mit Fiat-Geld (EUR, USD) unterstützen möchtest, kannst du Tokens auf Plasitkkarten im <b-link href="#">Shop</b-link>.
              </p>
            </div>
          </b-col>
          <b-col md="6">
            <div class="box">
              <p v-if="!addressExist"><i>rechts oben neben der Addressleiste deines Browswers befindet sich ein kleines Icon, welches einen orangen Fuchs zeigt. Klicke auf das Icon um MetaMask zu öffnen und folge den Anweisungen</i></p>
              <p v-if="etherBalance == 0"><i>Besuche <a href="https://faucet.metamask.io/" target="_blank">diese Webseite</a> um Ether zu erhalten. (Da wir uns noch im Test Netzwerk befindne, sind Ether kostenlos)</i></p>
              <p v-if="tokenBalance == 0"><i>öffne MetaMask und sende Ether an die Addresse {{crowdsaleAddress}} (Crowdsale&nbsp;Contract)</i></p>
            </div>
          </b-col>
        </b-row>
        <hr>
      </b-tab>
      <b-tab title="karte einlösen" >
        <br>
        <b-row>
          <b-col>
            <h3>variante 2: token von Karte übertragen</h3>
            <p>Plastikkarten sind eine schöne Möglichkeit um Token vor Ort an Helfer herauszugeben, falls diese noch keine Ethereum-Adesse haben. Man kann sie Freundne und Verwanten schenken. Außerdem können wir so Tokens gegen Bar- oder Fiat-Geld angeboten werden.<br><br>
              Du hast schon Token in Form einer Plastikkarte erhalten? Dann übertrage jetzt deinen <i>Private Key</i> in MetaMask</p>
          </b-col>
        </b-row>
        <hr>
        <br>
        <b-row>
          <b-col md="6">
            <div class="box">
              <ol>
                <li><i>private Key</i> aufrubbeln</li>
                <li>Metamask öffnen und <i>Import Existing DEN</i> wählen</li>
                <li>gebe deinen <i>private Key</i> ein und wähle ein Passwort für MetaMask</li>
              </ol>
              <p><br>Falls du schon einen Account in MetaMask erstellt hast, öffne das Menü oben rechts und wähle <i>Log Out</i>. Im Anschluss wähe <i>restore from seed phrase</i></p>
            </div>
          </b-col>
          <b-col md="6">
            <div class="box">
              <b-img center id="card" src="static/img/ether-cards3.png"></b-img>
            </div>
          </b-col>
        </b-row>
        <hr>
      </b-tab>
      <b-tab title="menschen treffen">
        <br>
        <b-row>
          <b-col>
            <h3>variante 3: gehe zu einem Treffen</h3>
            <p>um andere Spieler kennenzulernen, Ideen zu dikutieren, regionale Netzwerke zu gründen und Projekte zu starten.<br><br>
              Jeder Teilnehmer eines erfolgreichen Treffens erhält 5 Tokens.</p>
          </b-col>
        </b-row>
        <hr>
            <meetingForm></meetingForm>
        <hr>
      </b-tab>
      <b-tab title="aufgaben erledigen">
        <br>
        <b-row>
          <b-col>
        <ul>
          <li>Logo entwerfen</li>
          <li>Bloggen / Texte verfassen</li>
          <li>Texte übersetzen</li>
        </ul>
      </b-col>
      </b-row>
      </b-tab>
    </b-tabs>
    <b-button v-if="tokenBalance > 0">zu level 3</b-button>
    <br><br>
  </div>
</b-container>
</template>

<script>
import token from '@/js/erc20Token'
import crowdsale from '@/js/crowdsale'
import TopBar from './TopBar'
import MeetingForm from './MeetingForm'

export default {
  name: 'Level2',
  components: {
    TopBar,
    MeetingForm
  },
  data () {
    return {
      tokenBalance: 0,
      etherBalance: 0,
      crowdsaleAddress: ''
    }
  },
  computed: {
    network: function () {
      var string
      var netId = window.web3.version.network
      if (netId === '1') string = 'Ethereum Main Network'
      else if (netId === '3') string = 'Ropsten Test Network'
      else if (netId === '4') string = 'Rinkeby Test Network'
      else if (netId === '42') string = 'Kovan Test Network'
      else string = netId
      console.log('network:' + netId)
      return string
    },
    address: function () {
      return window.web3.eth.accounts[0]
    },
    web3Existe: function () {
      return window.web3Existe
    },
    addressExist: function () {
      return this.address !== undefined
    },
    ropstenConnected: function () {
      return this.network === 'Ropsten Test Network'
    }
  },
  beforeCreate: function () {
    if (window.web3.eth.accounts[0] !== undefined) {
      console.log('address: ' + window.web3.eth.accounts[0])
      token.init().then(() => {
        token.balanceOf(window.web3.eth.accounts[0]).then((balance) => {
          this.tokenBalance = balance / 1000000000000000000
        })
      }).catch(err => {
        console.log(err)
      })
      window.web3.eth.getBalance(window.web3.eth.accounts[0], (error, ebalance) => {
        this.etherBalance = ebalance.c[0] / 1000000000000000000
        console.log('ether: ' + ebalance / 1000000000000000000)
        if (error) {
          console.log(error)
        }
      })
    }
    crowdsale.init().then(() => {
      this.crowdsaleAddress = crowdsale.getAddress()
    }).catch(err => {
      console.log(err)
    })
    var account = window.web3.eth.accounts[0]
    setInterval(function () {
      if (window.web3.eth.accounts[0] !== account) {
        account = window.web3.eth.accounts[0]
        location.reload()
      }
    }, 100)
  },
  methods: {
  }
}
</script>

<style lang="scss" scoped>
#top_bar
{
  margin-top: -50px;
  margin-bottom: 50px;
}

#card
{
  width: 356px;
}

.solved
{
  font-weight: bold;
}
</style>



