<template>
  <b-container>
    <topBar></topBar>
<div>
  <b-row>
    <b-col>
      <h2>level 1</h2>
      <p>verbinde deinen Browser mit dem <i>Ethereum Test Netzwerk</i></p>
      <hr>
    </b-col>
  </b-row>
  <b-row>
    <b-col md="6">
      <div class="box">
        <ol>
          <li v-bind:class="{ solved: web3Existe }">installiere MetaMask <b-img v-if="web3Existe" src="static/open-iconic/svg/check.svg" alt="check"></b-img></li>
          <li v-bind:class="{ solved: ropstenConnected }">verbinde dich mit dem Ethereum Ropsten Testnet <b-img v-if="ropstenConnected" src="static/open-iconic/svg/check.svg" alt="check"></b-img></li>
        </ol>
      </div>
    </b-col>
    <b-col md="6">
      <div class="box">
        <p v-if="!web3Existe">besuche <a href="https://metamask.io/" target="_blank">metamask.io</a> um die MetaMask für deinen Browser herunterzuladen</p>
        <p v-if="!ropstenConnected"><i>öffne MetaMask und klicke oben rechts um eine Liste aller verfügbaren Netzwerke zu erhalten. Wähle das "Ropsten Test Netzwerk" aus</i></p>
      </div>
    </b-col>
  </b-row>
  <hr>
  <b-button v-if="web3Existe && ropstenConnected" href="./#/level2">zu level 2</b-button>
</div>
</b-container>
</template>

<script>
import TopBar from './TopBar'

export default {
  name: 'Level1',
  components: {
    TopBar
  },
  data () {
    return {
    }
  },
  computed: {
    network: function () {
      if (window.web3Existe) {
        var string
        var netId = window.web3.version.network
        if (netId === '1') string = 'Ethereum Main Network'
        else if (netId === '3') string = 'Ropsten Test Network'
        else if (netId === '4') string = 'Rinkeby Test Network'
        else if (netId === '42') string = 'Kovan Test Network'
        else string = netId
        console.log('network:' + netId)
        return string
      }
    },
    web3Existe: function () {
      return window.web3Existe
    },
    ropstenConnected: function () {
      return this.network === 'Ropsten Test Network'
    }
  }
}
</script>

<style lang="scss" scoped>
#top_bar
{
  margin-top: -50px;
  margin-bottom: 50px;
}

.solved
{
  font-weight: bold;
}
</style>



